




































































































export default {
	data() {
		return {
			buylist:[],		//订单列表
			goodsPrice:0.0,	//商品合计价格
			sumPrice:0.0,	//用户付款价格
			freight:12.00,	//运费
			note:'',		//备注
			int:1200,		//抵扣积分
			deduction:0,	//抵扣价格
			recinfo:{id:1,name:"大黑哥",head:"大",tel:"18816881688",address:{region:{"label":"广东省-深圳市-福田区","value":[18,2,1],"cityCode":"440304"},detailed:'深南大道1111号无名摩登大厦6楼A2'},isDefault:true}

		};
	},
	onShow(option) {
		// if(option.path&&option.path == 'cart'){
			uni.getStorage({
				key:'buylist',
				success: (ret) => {
					this.buylist = ret.data;
					this.goodsPrice=0;
					//合计
					let len = this.buylist.length;
					for(let i=0;i<len;i++){
						this.goodsPrice = this.goodsPrice + (this.buylist[i].number*this.buylist[i].price);
					}
					this.deduction = this.int/100;
					this.sumPrice = this.goodsPrice-this.deduction+this.freight;
					//强制保留两位小数
					this.sumPrice = this.sumPrice.toFixed(2);
					this.goodsPrice = this.goodsPrice.toFixed(2);
					this.freight = this.freight.toFixed(2);
					this.deduction = this.deduction.toFixed(2);
				}
			});
		// }
		//页面显示时，加载订单信息
		
		uni.getStorage({
			key:'selectAddress',
			success: (e) => {
				this.recinfo = e.data;
				uni.removeStorage({
					key:'selectAddress'
				})
			}
		})
	},
	onHide() {
		//页面隐藏清除订单信息
		console.log('页面隐藏');
		this.clearOrder();
	},
	onUnload() {
		console.log('取消订单');
		this.clearOrder();
	},
	onBackPress() {
		//页面后退时候，清除订单信息 //只适用于5+app h5
		this.clearOrder();
	},
	methods: {
		clearOrder(){
			uni.removeStorage({
				key: 'buylist',
				success: (res) => {
					this.buylist = [];
					console.log('remove buylist success');						
				}
			});
		},
		toPay(){
			//商品列表
			let paymentOrder = [];
			let goodsid=[];
			let len = this.buylist.length;
			for(let i=0;i<len;i++){
				paymentOrder.push(this.buylist[i]);
				goodsid.push(this.buylist[i].id);
			}
			if(paymentOrder.length==0){
				uni.showToast({title:'订单信息有误，请重新购买',icon:'none'});
				return ;
			}
			//本地模拟订单提交UI效果
			uni.showLoading({
				title:'正在提交订单...'
			})
			setTimeout(()=>{
				uni.setStorage({
					key:'paymentOrder',
					data:paymentOrder,
					success: () => {
						uni.hideLoading();
						uni.redirectTo({
							url:"../../payPage/payment/payment?amount="+this.sumPrice
						})
					}
				});//跳转到订单界面说明已经提交订单成功 订单已入库 则购物车不需要保留选中状态
			},1000)
			
		},
		//选择收货地址
		selectAddress(){
			uni.navigateTo({
				url:'../../userPage/address/address?type=select'
			})
		}
	}
}
